/**
 * @module app.control
*/
const exports = {};

import 'examples/control.css';
import './common_dependencies.js';
import ngeoMapModule from 'ngeo/map/module';

/** @suppress {extraRequire} */
import ngeoMiscControlComponent from 'ngeo/misc/controlComponent';

import olMap from 'ol/Map';
import olView from 'ol/View';
import olControlMousePosition from 'ol/control/MousePosition';
import olLayerTile from 'ol/layer/Tile';
import olSourceOSM from 'ol/source/OSM';


/** @type {!angular.Module} **/
app.module = angular.module('app', [
  ngeo.module.name,
  ngeoMapModule.name,
  ngeoMiscControlComponent.name,
]);


/**
 * @constructor
 *//* @ngInject
 */
app.MainController = function() {

  /**
   * @type {ol.Map}
   * @export
   */
  this.map = new olMap({
    layers: [
      new olLayerTile({
        source: new olSourceOSM()
      })
    ],
    view: new olView({
      center: [0, 0],
      zoom: 4
    })
  });

  /**
   * @type {ol.control.Control}
   * @export
   */
  this.control = new olControlMousePosition({
    className: 'mouse-position'
  });
};


app.module.controller('MainController', app.MainController);


export default exports;



